body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}
.wrapper {
    padding-top: 50px;
    background: #d81b60;
    height: 100%;
    display: flex;
    flex-flow: row wrap;
    padding: 20px;
    justify-content: space-around;
    align-content: flex-start;
    overflow: auto;
  }
.card {
    background: #fff;
    border-radius: 2px;
    height: 200px;
    margin: 10px;
    position: relative;
    width: 200px;
    box-shadow: 0 3px 6px #999, 0 3px 6px #999;
    align-content: center;
  }
  
  .card > .img-container {
    height: 100%;
    overflow: hidden;
    background:white;
  }
  
  
  .cusImg {
    width: 100%;
    text-align: center;
    padding: 30px;
    position: absolute;
  }
  
  .imgStyle {
      display:inline-block;
      border:0;
      width:200px;
      height:200px;
      position: relative;
      -webkit-transition: all 200ms ease-in;
      -webkit-transform: scale(1); 
      -ms-transition: all 200ms ease-in;
      -ms-transform: scale(1); 
      -moz-transition: all 200ms ease-in;
      -moz-transform: scale(1);
      transition: all 200ms ease-in;
      transform: scale(1);   
  
  }
  
  .imgStyle:hover {
      cursor:pointer;
      box-shadow: 0px 0px 200px #000000;
      z-index: 2;
      -webkit-transition: all 200ms ease-in;
      -webkit-transform: scale(1.3);
      -ms-transition: all 200ms ease-in;
      -ms-transform: scale(1.3);   
      -moz-transition: all 200ms ease-in;
      -moz-transform: scale(1.3);
      transition: all 200ms ease-in;
      transform: scale(1.3);
  }
  
  .imgStylePrevious {
    -webkit-animation: shake 0.2s;
            animation: shake 0.2s;
    -webkit-animation-iteration-count: 1;
            animation-iteration-count: 1;
  }
.App {
  text-align: center;
}

.App-header {
  width: 100%;
  overflow: hidden;
  color: white;
}

.App-title {
  color: black;
}

.message {
  font-size: 10pt;
  color: black;
}

.Jumbotron {
  background-color: #d81b60;
}


